<script>
	let email = ''
	let message = ''

	const sendEmail = (e) => {
		e.preventDefault()

		if(email != '' || message != '') {
			let link = `mailto:razfasouhaila20@gmail.com?cc=${email}&subject=Creation%20de%20projet&body=${escape(message)}`
			window.location.href = link
			
			email = ''
			message = ''
		}
	}
</script>

<div class="section">
	<h3><i class="icon-hireme"></i>Engagez moi</h3>

	<form>	
		<label for="email">Email</label>
		<input type="email" bind:value={ email } id="email" placeholder="your email here" required>

		<label for="message">Message</label>
		<textarea id="message" placeholder="Message text" bind:value={ message } required></textarea>

		<button type="submit" on:click={ sendEmail }>Engagez moi</button>
	</form>
</div>

